<template>
  <div class="result">
    <!-- 头图 -->
    <div class="header">
      <img :src="`${baseOssUrl}header.jpg`" alt="" />
    </div>
    <!-- 详情 -->
    <div class="detail">
      <img :src="`${baseOssUrl}detail1.jpg`" alt="" />
      <img
        :src="`${baseOssUrl}detail2.jpg`"
        alt="" />
      <img
        :src="`${baseOssUrl}detail3.jpg`"
        id="target"
        alt="" />
      <div class="getBtn" @click="copy(0)">一键获取</div>
    </div>
    <!-- 步骤 -->
    <div class="steps">
      <img :src="`${baseOssUrl}stepBg1.jpg`" alt="" />
      <img :src="`${baseOssUrl}stepBg2.jpg`" alt="" />
      <img :src="`${baseOssUrl}stepBg3.jpg`" alt="" />
      <VanSwipe
        class="swiper-container swiper1"
        :autoplay="3000"
        indicator-color="white">
        <VanSwipeItem>
          <img
            :src="`${baseOssUrl}swiper2-0.png`"
            alt="">
        </VanSwipeItem>
        <VanSwipeItem>
          <img
            :src="`${baseOssUrl}swiper2-1.png`"
            alt="">
        </VanSwipeItem>
      </VanSwipe>
      <VanSwipe
        class="swiper-container swiper2"
        :autoplay="3000"
        indicator-color="white">
        <VanSwipeItem>
          <img
            :src="`${baseOssUrl}swiper3-0.png`"
            alt="">
        </VanSwipeItem>
        <VanSwipeItem>
          <img
            :src="`${baseOssUrl}swiper3-1.png`"
            alt="">
        </VanSwipeItem>
      </VanSwipe>
    </div>
    <footer>
      <img
        :src="`${baseOssUrl}footerBtn.png`"
        @click="goTarget()"
        alt="">
    </footer>
    <VanDialog
      class="dialog"
      v-model="show"
      @close="toClose"
      :show-confirm-button="false"
      :close-on-click-overlay="true">
      <img :src="`${baseOssUrl}dialog_bg.png`" />
      <img
        src="@/assets/TwelveMp/close.png"
        alt=""
        @click="toClose"
        class="close" />
      <div class="dialog-copyBtn" @click="copy(1)">点我获取公众号二维码</div>
    </VanDialog>
  </div>
</template>
<script>
import { copyText, getSearch, goAnchor } from '@/utils/index.js'
import preventBack from '@/mixins/preventBack'
const dataConfig = {
  0: {
    module_ori: '首屏复制按钮',
    module_part: '106_toufang_lm_0340'
  },
  1: {
    module_ori: '挽留弹窗',
    module_part: '106_tf_h5_00010'
  }
}
export default {
  mixins: [preventBack],
  data () {
    return {
      show: false,
      baseOssUrl: 'http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/TwelveMp/result/'
    }
  },
  mounted () {
    this.$on('userBack', () => {
      this.$log.click({
        module_ori: '挽留弹窗',
        module_part: '106_tf_h5_00008'
      })
      this.show = true
    })
  },
  methods: {
    goAnchor,
    goTarget () {
      this.$log.click({
        module_ori: '悬浮按钮',
        module_part: '106_toufang_lm_0341'
      })
      this.goAnchor('#target')
    },
    toClose () {
      this.$log.click({
        module_ori: '挽留弹窗',
        module_part: '106_tf_h5_00009'
      })
      this.show = false
    },
    async copy (key) {
      this.$log.click(dataConfig[key])
      const query = getSearch()
      try {
        copyText('新手干货社', '复制成功')
        await this.$apis.Tiktok.copy({
          ...query,
          vage: 1
        })
        const { url } = await this.$apis.userOperator.getMiniHref({
          path: 'xby/pages/qrcode/index',
          appid: 'wxdd51b8f4efa83b83',
          query
        })
        window.location.href = url
      } catch (error) {
        console.log(error)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.result {
  background: #e5e5e5;
  padding-bottom: 200px;

  img {
    width: 100%;
  }

  a {
    display: inline-block;
  }

  .dialog {
    background: transparent;
    text-align: center;
    min-height: 427px;

    .close {
      width: 90px;
      height: 90px;
      margin-top: 20px;
    }

    &-copyBtn {
      position: absolute;
      background: #1eb004;
      width: 70%;
      height: 90px;
      left: 50%;
      transform: translateX(-50%);
      color: #fff;
      bottom: 22%;
      border-radius: 60px;

      @include horVerCenter;
    }
  }

  .detail {
    position: relative;

    .getBtn {
      position: absolute;
      width: 172px;
      height: 57px;
      background: linear-gradient(0deg, #fd6f49, #fa5424);
      right: 108px;
      bottom: 70px;
      border-radius: 50px;
      color: #fff;
      font-size: 20px;
      animation: btnScale 1s infinite;

      @include horVerCenter;
    }
  }

  .steps {
    position: relative;

    .swiper1 {
      top: 1020px;
    }

    .swiper2 {
      bottom: 130px;
    }
  }

  .swiper-container {
    width: 580px;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
  }

  footer {
    padding: 10px 60px;
    position: fixed;
    bottom: 0;
    width: 750px;
    box-sizing: border-box;
  }
}

@keyframes btnScale {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}
</style>
